// {"title":"AngleSharp v0.9.1 released","date":"2015-09-16 11:08:00","categories":"news release","author":"Florian Rappl","link":"/blog/2015-09-16-anglesharp-v091-released","source":"E:\\Code\\AngleSharp\\AngleSharp.Website\\docs\\blog\\2015-09-16-anglesharp-v091-released.md"}
import * as React from 'react';
import { ContentPage } from '../../scripts/components';

      import { PageContent, Markdown } from '../../scripts/components';

      const link = "https://github.com/AngleSharp/anglesharp.github.io/tree/main/docs/blog/2015-09-16-anglesharp-v091-released.md";
      const html = `
<p>A week ago the the first patch for AngleSharp v0.9.1 has been released. Besides fixing some issues the event loop model has been reworked. This will not be the last update to this mechanism. The next update v0.9.2 will focus on closing some existing issues, such as the proposed <code>CssNode</code>.</p>
<p>One of the most important additions to AngleSharp v0.9.1 is the ability to filter (http, data, ...) requests. The standard requester service has been extended to provide the ability for this integration. It is therefore possible to stop unwanted requests directly without having to provide a custom <code>IRequester</code> implementation.</p>
<p>Finally a remark for the upcoming releases: The service model will definitely change until v1. Right now there are too many interfaces and layers required to extend AngleSharp. Also the whole approach won't scale well once loosely coupled abilities, such as drawing or performance capturing, arrive. I believe that methods may be happier if they can just send a message, which may or may not find a listener. These messages will be very loosely coupled.</p>
<p>At the moment I don't know if such a potential change will also affect the event aggregator, but only using DOM events may actually be a good thing. In the end the <code>BrowsingContext</code> itself may end up as an <code>EventTarget</code>, which would bring consistency across the entire library.</p>
`;
    

export default () => (
  <ContentPage>
    
      <PageContent meta={{"title":"AngleSharp v0.9.1 released","date":"2015-09-16 11:08:00","categories":"news release","author":"Florian Rappl"}}>
        <Markdown content={html} link={link} editLabel={"Edit on GitHub"} />
      </PageContent>
    
  </ContentPage>
);